.SignaturePad {
  align-items: center;
  background: #114365;
  border-radius: 5px;
  border: 2px solid #21789d;
  box-shadow: inset 0px 0px 5px 2px #061f40;
  display: flex;
  justify-content: center;
  margin: 30px 0;
  padding: 15px;
}
.btn-signaturePad {
  background: #114365;
  border: 2px solid #21789d;
  color: #fff;
}
