/**
 *
 * LOGIN
 *
 */
.login {
  background-size: cover;
  background: #fff;
  bottom: 0;
  display: flex;
  flex-direction: column;
  left: 0;
  overflow-y: auto;
  position: fixed;
  right: 0;
  top: 0;
  z-index: 9;
}
.login-saludo {
  text-align: center;
  margin: 35px 0;
  overflow: hidden;
  color: #166099;
  animation: tracking-in-contract-bck-top 1s cubic-bezier(0.215, 0.61, 0.355, 1) both;
}
.login-header {
  align-items: center;
  justify-content: center;
  padding: 25px;
  text-align: center;
  background: #1554b1 url(images/bg-header-login.79cb978.png) no-repeat left top;
  clip-path: polygon(0 0, 100% 0, 100% 20%, 100% 80%, 75% 66%, 39% 92%, 0% 80%, 0% 20%);
}
.login-header-logo {
  display: inline-block;
  width: 200px;
  animation: puff-in-ver 0.7s cubic-bezier(0.47, 0, 0.745, 0.715) both;
}
.login-header-title {
  color: #eee;
  padding: 0 55px;
  display: block;
  width: 100%;
  margin: 10px;
}
.login-body {
  flex: 1;
  padding-bottom: 50px;
}
.login-footer {
  background: #0053b5;
  text-align: center;
  padding: 15px;
  position: relative;
  color: #fff;
}
.login-footer-pw {
  color: #fff;
  font-weight: 700;
  font-size: 18px;
}
.login-footer-version {
  position: absolute;
  bottom: 5px;
  right: 5px;
  font-size: 10px;
  color: #ccc;
}
.login .form-group label {
  color: #166099;
}
.login .form-group .form-control {
  background: transparent;
  box-shadow: none;
  border: 2px solid #166099;
  color: #03A9F4;
}
.swiper-container {
  width: 100%;
  height: 100%;
}
.puff-in-ver {
  animation: puff-in-ver 0.7s cubic-bezier(0.47, 0, 0.745, 0.715) both;
}
@keyframes puff-in-ver {
  0% {
    transform: scaleY(2);
    filter: blur(2px);
    opacity: 0;
  }
  100% {
    transform: scaleY(1);
    filter: blur(0px);
    opacity: 1;
  }
}
.tracking-in-contract-bck-top {
  animation: tracking-in-contract-bck-top 1s cubic-bezier(0.215, 0.61, 0.355, 1) both;
}
@keyframes tracking-in-contract-bck-top {
  0% {
    letter-spacing: 1em;
    transform: translateZ(400px) translateY(-300px);
    opacity: 0;
  }
  40% {
    opacity: 0.6;
  }
  100% {
    transform: translateZ(0) translateY(0);
    opacity: 1;
  }
}
