.botonesFooter {
  position: absolute;
  bottom: 100px;
  width: 80vw;
  z-index: 9;
  left: 50%;
  transform: translateX(-50%);
  text-align: center;
}
.swiperInit {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: linear-gradient(0deg, #061a31, #105099 20%);
  z-index: 8;
}
.swiperInit .swiper-slide {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  padding: 35px;
  color: #fff;
  font-size: 22px;
}
.swiperInit .swiper-slide.slide-1 {
  background: #ccc url(images/slide-1.0b9e665.jpeg) no-repeat center center;
  background-size: cover;
}
.swiperInit .swiper-slide.slide-2 {
  background: #ccc url(images/slide-2.9c08488.jpeg) no-repeat center center;
  background-size: cover;
}
.swiperInit .swiper-slide.slide-3 {
  background: #ccc url(images/slide-3.89bf33a.jpg) no-repeat center center;
  background-size: cover;
}
