.camara {
  align-items: center;
  background: #eeeeee;
  border: 2px dashed #dcdcdc;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 20px 0;
  padding: 10px;
  border-radius: 5px;
  box-shadow: inset 0px 0px 5px #c5c5c5;
  color: #000;
}
.camara-label {
  color: #505050;
}
.camara img {
  display: inline-block;
  width: 80%;
  border-radius: 10px;
}
.camara img.fotoINE {
  width: 100%;
}
