/**
 *
 * APP
 *
 */
@font-face {
  font-family: 'Abel';
  src: url(fonts/abel-regular-webfont.4c30207.woff2) format('woff2'), url(fonts/abel-regular-webfont.f227f5d.woff) format('woff');
  font-weight: normal;
  font-style: normal;
}
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
html {
  touch-action: manipulation;
}
body {
  -webkit-touch-callout: none;
  -webkit-text-size-adjust: none;
  -webkit-user-select: none;
  padding-top: constant(safe-area-inset-top);
  /* iOS 11.0 */
  padding-top: env(safe-area-inset-top);
  background: #eee url(images/bg-quilted.c975942.jpg);
  font-family: 'Abel', sans-serif;
  max-width: 768px;
  margin: 0 auto;
  color: #423f40;
}
.hide {
  display: none;
}
.loading {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 9999;
  background: #fbfbfb;
  display: flex;
  justify-content: center;
  align-content: center;
  animation: fade-in-fwd 0.6s cubic-bezier(0.39, 0.575, 0.565, 1) both;
}
.loading.oculto {
  animation: fade-out-bck 1.2s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
  z-index: -1;
}
.loading img {
  display: block;
  width: 100vw;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.flip-vertical-right {
  animation: flip-vertical-right 1.7s cubic-bezier(0.455, 0.03, 0.515, 0.955) 0.5s infinite alternate;
}
@keyframes flip-vertical-right {
  0% {
    transform: rotateY(0);
  }
  100% {
    transform: rotateY(180deg);
  }
}
.card .card-title {
  text-align: center;
  background: #104f98;
  color: #fff;
  margin: 0 0 25px 0;
  padding: 4px 8px;
  border-radius: 3px;
  font-weight: 400;
  box-shadow: inset 0px 0px 5px 1px #08315e;
  font-size: 18px;
}
.btn {
  border-radius: 20px;
  text-transform: uppercase;
}
.btn.btn-cuadrado {
  border-radius: 0;
}
.form-group > label {
  font-weight: 900;
  color: #000;
  font-size: 18px;
}
.form-group .form-control {
  font-weight: 600;
  text-align: center;
  background: #fff;
  border-radius: 15px;
  text-align-last: center;
  color: #555;
  letter-spacing: 1px;
  border: 0;
  box-shadow: inset 0 0 10px 1px #e2e2e2;
  transition: all 0.2s linear;
}
.form-group .form-control:disabled {
  background: #eee;
}
.form-group .form-control.default {
  color: #1b1b1b;
  background: #e2e2e2;
}
.form-group .form-control:focus {
  border: 2px solid #dfdfdf;
  background: #fff;
  color: #000;
  box-shadow: none;
}
.form-group .form-icon {
  position: relative;
}
.form-group .form-icon > img {
  position: absolute;
  top: 0;
  right: 10px;
  height: 100%;
}
.tracking-in-expand {
  animation: tracking-in-expand 0.7s cubic-bezier(0.215, 0.61, 0.355, 1) both;
}
@keyframes tracking-in-expand {
  0% {
    letter-spacing: -0.5em;
    opacity: 0;
  }
  40% {
    opacity: 0.6;
  }
  100% {
    opacity: 1;
  }
}
.fade-out-bck {
  animation: fade-out-bck 1.2s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
}
@keyframes fade-out-bck {
  0% {
    transform: translateZ(0);
    opacity: 1;
  }
  100% {
    transform: translateZ(-80px);
    opacity: 0;
  }
}
.fade-in-fwd {
  animation: fade-in-fwd 0.6s cubic-bezier(0.39, 0.575, 0.565, 1) both;
}
@keyframes fade-in-fwd {
  0% {
    transform: translateZ(-80px);
    opacity: 0;
  }
  100% {
    transform: translateZ(0);
    opacity: 1;
  }
}
:focus {
  outline: none !important;
}
a {
  color: #2196F3;
}
h1,
h2,
h3,
h4,
h5 {
  font-family: 'Abel', sans-serif !important;
  font-weight: 700;
}
.modal-content {
  background: #011844;
}
.modal-content .modal-header {
  background: linear-gradient(180deg, #105099, #08305d) !important;
  color: #fff;
  border-bottom-color: #00fafb;
}
.modal-content .modal-header .modal-title {
  font-weight: 400;
}
/**
 *
 * LAYOUT
 *
 */
.menu {
  background: #fff;
  box-shadow: 0px 0px 5px 5px rgba(0, 0, 0, 0.3);
  display: flex;
  flex-direction: column;
  height: 100vh;
  transform: translateX(-90vw);
  overflow-y: auto;
  position: fixed;
  top: 0;
  width: 80vw;
  z-index: 99999;
  transition: all 0.2s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}
.menu.activo {
  transform: translateX(0);
}
.menu-header {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  flex-direction: column;
  padding: 15px 0;
}
.menu-header-img {
  display: block;
  width: 160px;
}
.menu-header-usr {
  color: #ccc;
  margin: 5px 0;
  font-size: 13px;
}
.menu-body {
  flex: 1;
  position: relative;
}
.menu-footer {
  position: relative;
}
.menu-nav {
  position: relative;
  margin: 0;
}
.menu-nav-item {
  display: block;
}
.menu-nav-item a {
  align-items: center;
  background: linear-gradient(45deg, #ffffff, #f4f4f4);
  color: #000;
  display: flex;
  justify-content: space-between;
  padding: 15px 25px;
}
.menu-nav-item a img {
  display: inline-block;
  width: 34px;
  margin-right: 16px;
}
.header {
  background: linear-gradient(45deg, #105099, #08305d);
  display: flex;
  padding: 5px 0;
  justify-content: center;
  align-items: center;
  border-bottom: 1px solid #00fafb;
}
.header-item {
  flex: 1;
  text-align: center;
}
.header-item img {
  display: inline-block;
  width: 22px;
}
#renderBody {
  padding: 15px;
}
