/**
 *
 * ORDENES
 *
 */
.ordenes .orden {
  animation: slit-in-horizontal 0.45s ease-out both;
}
.ordenes .orden .card-header {
  text-align: center;
  font-size: 22px;
}
.ordenes .orden.orden-estatus-2 .card-header {
  background: linear-gradient(45deg, #104f98, #2c82e4);
  color: #fff;
}
.ordenes .orden.orden-estatus-3 .card-header {
  background: linear-gradient(45deg, #009688, #2c82e4);
  color: #fff;
}
.ordenes .orden.orden-estatus-4 .card-header {
  background: linear-gradient(45deg, #ffc107, #e0b228);
  color: #fff;
}
.ordenes .orden.orden-estatus-5 .card-header {
  background: linear-gradient(45deg, #8BC34A, #CDDC39);
  color: #fff;
}
.slit-in-horizontal {
  animation: slit-in-horizontal 0.45s ease-out both;
}
@keyframes slit-in-horizontal {
  0% {
    transform: translateZ(-800px) rotateX(90deg);
    opacity: 0;
  }
  54% {
    transform: translateZ(-160px) rotateX(87deg);
    opacity: 1;
  }
  100% {
    transform: translateZ(0) rotateX(0);
  }
}
#semaforos img {
  animation: fade-in-top 0.6s cubic-bezier(0.39, 0.575, 0.565, 1) both;
}
.fade-in-top {
  animation: fade-in-top 0.6s cubic-bezier(0.39, 0.575, 0.565, 1) both;
}
@keyframes fade-in-top {
  0% {
    transform: translateY(-50px);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}
.estatus {
  position: relative;
  width: 30px;
  height: 30px;
  display: inline-block;
  margin-bottom: 6px;
}
.estatus b {
  position: absolute;
  z-index: 9;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 13px;
}
.estatus-orden {
  animation: estatusOrden 2s ease-in-out both;
  background: radial-gradient(#fff, #ccc);
  border-radius: 50%;
  border: 2px solid #053975;
  clip-path: polygon(0% 0%, 0% 100%, 25% 100%, 25% 25%, 75% 25%, 75% 75%, 25% 75%, 25% 100%, 100% 100%, 100% 0%);
  height: 30px;
  margin: 0 auto;
  width: 30px;
}
.estatus-orden.estatus-2 {
  background: radial-gradient(#104f98, #2c82e4);
}
.estatus-orden.estatus-3 {
  background: radial-gradient(#1ff500, #2c82e4);
}
.estatus-orden.estatus-4 {
  background: radial-gradient(#ffc107, #e0b228);
}
.estatus-orden.estatus-5 {
  background: radial-gradient(#8BC34A, #CDDC39);
}
.estatus + small {
  font-weight: 600;
  line-height: 1;
  display: block;
}
@keyframes estatusOrden {
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(45deg);
  }
}
.detalleOrden-hgroup {
  text-align: center;
}
.detalleOrden-title {
  display: inline-block;
  border-radius: 10px;
  background: #f8f9fa;
  padding: 4px 15px;
  color: #000;
  margin: 0 auto;
}
