.selfie {
  position: relative;
  width: 200px;
  height: 200px;
  border-radius: 50%;
  overflow: hidden;
  margin: 0 auto 20px auto;
  background: #fff;
  box-shadow: inset 0px 0px 5px 1px #e0e0e0;
  border: 2px solid #fff;
}
.selfie-label {
  font-size: 22px;
}
.selfie-img {
  position: absolute;
  top: 0;
  left: 0;
  width: 200px;
}
