.tarjetas .tarjeta {
  background: #fff;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  margin: 0px 0px 30px 0;
  padding: 15px;
}
.tarjetas .tarjeta-img {
  display: inline-block;
  width: 100%;
  margin-bottom: 15px;
  animation: roll-in-blurred-left 0.65s cubic-bezier(0.23, 1, 0.32, 1) both;
}
.tarjetas .tarjeta-saldo {
  color: #333;
  margin: 0 0 25px 0;
}
.tarjetas .tarjeta-saldo-monto {
  font-size: 32px;
  color: #38588e;
  text-align: center;
  font-weight: 900;
}
.nav-pills .nav-link {
  background-color: #fbfbfb;
  color: #061f40;
}
.nav-pills .nav-link.active {
  background-color: #fff;
  border-bottom: 4px solid #7fdaff;
  border-radius: 0;
  color: #061f40;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 1px;
}
.roll-in-blurred-left {
  animation: roll-in-blurred-left 0.65s cubic-bezier(0.23, 1, 0.32, 1) both;
}
@keyframes roll-in-blurred-left {
  0% {
    transform: translateX(-1000px) rotate(-720deg);
    filter: blur(50px);
    opacity: 0;
  }
  100% {
    transform: translateX(0) rotate(0deg);
    filter: blur(0);
    opacity: 1;
  }
}
div.ioslide {
  position: relative;
}
input.iostyle[type=checkbox] {
  -webkit-appearance: none;
  display: block;
  position: relative;
  width: 100px;
  height: 30px;
  background: #8BC34A;
  border-radius: 30px;
  box-shadow: 0px -3px 1px 2px rgba(0, 0, 0, 0.1), 0px 3px 1px 2px rgba(255, 255, 255, 0.9), inset 1px 3px 4px rgba(0, 0, 0, 0.35), inset 1px -1px 2px rgba(0, 0, 0, 0.2);
  cursor: pointer;
  -webkit-transition: all 0.2s;
}
div.ioslide label {
  display: block;
  position: absolute;
  top: 0;
  margin-top: -7px;
  margin-left: 70px;
  background: #000;
  height: 40px;
  width: 40px;
  border-radius: 100%;
  background: -webkit-linear-gradient(#fbfbfb 0%, #e4e4e4 100%);
  border: 1px solid rgba(0, 0, 0, 0.1);
  box-shadow: inset 0px 1px 1px #ffffff, inset 0px -1px 20px 0px rgba(0, 0, 0, 0.1), 0px 3px 7px rgba(0, 0, 0, 0.4);
  z-index: 100;
  cursor: pointer;
  -webkit-transition: margin-left 0.5s;
}
div.ioslide label:before {
  display: block;
  content: "";
  position: absolute;
  left: 14px;
  top: 15px;
  height: 10px;
  width: 10px;
  border-radius: 100%;
  background: #8BC34A;
  border: 1px solid rgba(255, 255, 255, 0.7);
  box-shadow: inset 0px 1px 2px rgba(0, 0, 0, 0.4), 0px 1px 1px #ffffff;
  z-index: 80;
}
input.iostyle[type=checkbox]:checked + label {
  margin-left: -7px;
}
input.iostyle[type=checkbox]:checked + label:before {
  background: #9a9a9a;
}
input.iostyle[type=checkbox]:before {
  display: block;
  content: "SI";
  font-size: 13pt;
  color: #ffe;
  position: absolute;
  left: 30px;
  top: 3px;
  height: 10px;
  width: 10px;
  border-radius: 100%;
  z-index: 80;
  text-shadow: 0px 1px 1px rgba(0, 0, 0, 0.3);
}
input.iostyle[type=checkbox]:checked {
  background: #9a9a9a;
}
input.iostyle[type=checkbox]:checked:before {
  content: "NO";
  left: 45px;
}
